	import popModal from '../../../../components/popmodal.vue'
	export default{
		data(){
			return{
				modalName:null,
				radio: 'radio1',
				shopItem:'',
				isShow:false,
				designer:{
					name:'',
					telephone:''
				},
				userList:[],//店员列表
			}
		},
		components:{
			popModal
		},
		methods:{
			//验证电话号码
			checkTelEvent(event){
				console.log(event)
				if(event){
					if(!(/^1[34578]\d{9}$/.test(event))){
							console.log('222')
							uni.showModal({
								content:'电话号码不存在,请重新'
							})
						}
					}
				},
			//设置店长
			setShopManager(){},
			//邀请加入
			inviteJoin(){
				this.isShow=true;
				this.designer.name='';
				this.designer.telephone=''
			},
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
			},
			hideModal(){
				this.isShow=false;
			},

			RadioChange(e) {
				this.radio = e.detail.value
			},
			//获得门店详情
			checkShopDetail(id){
			    uni.request({
					url:this.$store.state.url+'ProprietorShop',
					data:{
					    id:id,
						// userId:this.$store.state.userInfo.id,
						// owner:this.$store.state.userInfo.owner
						userId:49,
						owner:18
					},
					success:(res)=>{
					    this.shopItem=res.data.data;
					    console.log(this.shopItem)
					}
				})
			},
			//录入店员
			recordUser(){
				if((/^1[34578]\d{9}$/.test(event))){
						this.isShow=false;
						uni.showToast({
							title:'电话号码不存在',
							icon:'none'
						})

				}else if(!this.designer.name){
					uni.showToast({
						title:'请输入店员姓名',
						icon:'none'
					})
				}else{
					uni.request({
						url:this.$store.state.url+'Signup',
						data:{
							name:this.designer.name,
							mobile:this.designer.telephone,
							type:this.$store.state.userInfo.type,
							team:this.shopItem.id
						},
						success: (res) => {
							uni.showToast({
								title:'录入店员成功',
								icon:'none'
							});
							let obj={
								name:this.designer.name,
								telephone:this.designer.telephone,
								id:res.data.data
							}
							this.userList=[obj,...this.userList];
							console.log(this.userList)
							this.isShow=false;
						}
					})
				}

			}
		},
		onLoad(option){
		   if(option){
               this.checkShopDetail(option.shopID)
		   }
		}
	}
